.feed {
    flex: 0.7;
    margin: 0 20px;
}

.feed__inputContainer {
    background-color: white;
    padding: 10px;
    padding-bottom: 20px;
    border-radius: 10px;
    margin-bottom: 20px;
}

.lit {}

.opt {
    width: 100%;
    height: 100px;
}

.feed__input {
    border: 1px solid lightgray;
    border-radius: 30px;
    display: flex;
    padding: 10px;
    color: gray;
    padding-left: 15px;
}

.feed__input>form {
    /* display: flex; */
    width: 100%;
}

.feed__input>form>input {
    border: none;
    flex: 1;
    margin-left: 10px;
    outline-width: 0;
    font-weight: 600;
}

.feed__input>form>button {
    display: none;
}

.feed__inputOptions {
    display: flex;
    justify-content: space-evenly;
}

hr {
    margin-block-start: 16px;
    margin-block-end: 16px;
}

@media screen and (max-width: 750px) {
    .feed_inputOptions {
        font-size: 13px;
    }
}